.photo:nth-child(2) {
  grid-column: 2/4;
  grid-row: span 2;
}

@media (max-width: 40rem) {
  .photo:nth-child(2) {
    grid-column: initial;
    grid-row: initial;
  }
}
.photo {
  display: grid;
  border-radius: 0.2rem;
  overflow: hidden;
  cursor: pointer;
}
.photo > div {
  grid-area: 1/1;
}
.visualizacao {
  background: rgba(0, 0, 0, 0.3);
  color: white;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: none;
  grid-area: 1/1;
}
.visualizacao::before {
  content: '';
  width: 16px;
  height: 10px;
  display: inline-block;
  background: url('../../Assets//visualizacao.svg') no-repeat;
  margin-right: 0.25rem;
}
.photo:hover .visualizacao {
  display: flex;
}
